'use strict';

// MODULES //

import { isPrimitive as isString } from '@stdlib/assert-is-string';
import isFunction from '@stdlib/assert-is-function';
import format from '@stdlib/error-tools-fmtprodmsg';


// MAIN //

/**
* Attempts to parse a string as JSON.
*
* @param {string} str - string to parse
* @param {Function} [reviver] - transformation function
* @throws {TypeError} first argument must be a string
* @throws {TypeError} reviver must be a function
* @returns {(*|Error)} parsed value or parse error
*
* @example
* var obj = parseJSON( '{"beep":"boop"}' );
* // returns {'beep':'boop'}
*/
function parseJSON( str, reviver ) {
	if ( !isString( str ) ) {
		throw new TypeError( format( '0l93R', str ) );
	}
	if ( arguments.length > 1 ) {
		if ( !isFunction( reviver ) ) {
			throw new TypeError( format( '0l9Ca', reviver ) );
		}
	}
	try {
		return JSON.parse( str, reviver );
	} catch ( error ) {
		return error;
	}
}


// EXPORTS //

export default parseJSON;
